@import url(./font.css);
@import url(./section.css);

.dot{
    opacity: 0;
    width: 180px;
    padding: 10px;
    color:  white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Next;
    z-index: 100;

    /*TEMP*/
    top: 336px;
    left: 353px;
}
.dot-item-span{
    font-size: 18px;
    color: rgb(217, 217, 217);
}
.dot-item-title{
    font-family: Next;
    font-weight:lighter;
    font-size: 25px;
    margin-bottom: 10px;
}
.dot-item-icon{
    border-radius: 50%;
    outline: solid white 2px;
    display:  grid;
    padding: 10px;
    place-content: center;
    
}
.dot-item-icon img{
    object-fit: cover;
    width:  50px;
    height: 50px;
}

.dot-item-icon {
    transition: transform 0.5s ease;
  }

  .dot-item-icon:hover {
    transform: scale(1.2);
    outline: solid rgb(71, 255, 221) 2px;
  }
